import {useState} from 'react';
import axios from 'axios';
import {ip, port} from "../url";

const {Kakao} = window

const SocialLogInKakao = () => {
    const [isLogin, setIsLogIn] = useState("false");

    const loginWithKakao = () => {
        try {
            return new Promise((resolve, reject) => {
                console.log(Kakao)
                if (!Kakao) {
                    reject('Kakao   .')
                }
                Kakao
                    .Auth
                    .login({
                        success: (auth) => {
                            console.log(`auth.access_token => ${auth.access_token}`)
                            axios
                                .post(ip + port + '/kakao/auth', {token: auth.access_token})
                                .then((res) => {
                                    axios.post(ip + port + '/user/signup', {
                                        email: `Kakao_${res.data.id}`,
                                        username: `${res.data.properties.nickname}`,
                                        password: `${res.data.id}`
                                    })

                                    axios
                                        .post(ip + port + '/user/signin', {
                                            email: `Kakao_${res.data.id}`,
                                            password: `${res.data.id}`
                                        })
                                        .then((res) => {
                                            const {accessToken} = res.data;
                                            axios
                                                .defaults
                                                .headers
                                                .common["Authorization"] = `Bearer ${accessToken}`;
                                            localStorage.setItem("token", accessToken);
                                        })
                                })

                            console.log('  .', auth)
                            setIsLogIn("true")
                        },
                        fail: (err) => {
                            console.log(err);
                        }
                    })
            })
        } catch (err) {
            console.log(err);
        }
    }

    const logoutWithKakao = () => {
        if (Kakao.Auth.getAccessToken()) {
            console.log('    .', Kakao.Auth.getAccessToken())
            Kakao
                .Auth
                .logout(() => {
                    console.log(' ', Kakao.Auth.getAccessToken());
                    this.setState({isLogin: false})
                    console.log('    .', Kakao.Auth.getAccessToken())
                });
        }
    }

    return (
        <div>
            <div>d</div>
            <div>d</div>
            <div>d</div>
            <div>d</div>
            <div>d</div>
            <div>d</div>
            <p></p>
            <button onClick={loginWithKakao}> </button>
        </div>
    )
}

export default SocialLogInKakao;

