import {useEffect} from 'react';
import axios from 'axios';
import {ip, port} from './../url';
import qs from 'qs';
function SocialLogin({history, location}) {
    useEffect(() => {
        async function getToken() {
            console.log(`location.search=${location.search}`)
            const {code} = qs.parse(location.search, {ignoreQueryPrefix: true});
            try {
                const accessToken = await axios.post(`${ip}${port}/github/auth`, {code})

                axios.post(ip + port + '/user/signup', {
                    email: `Github_${accessToken.data.id}`,
                    username: `${accessToken.data.login}`,
                    password: `${accessToken.data.id}`
                })
                axios
                    .post(ip + port + '/user/signin', {
                        email: `Github_${accessToken.data.id}`,
                        password: `${accessToken.data.id}`
                    })
                    .then((res) => {
                        const {accessToken} = res.data;
                        axios
                            .defaults
                            .headers
                            .common["Authorization"] = `Bearer ${accessToken}`;
                        localStorage.setItem("token", accessToken);
                    })

                console.log(' github/auth   ', accessToken.data);

                history.push('/'); 
                console.log('  ')
            } catch (error) {
            }
        }
        getToken();
    }, [location, history]);
    return null; 
}
export default SocialLogin;