import axios from "axios";
import { withRouter, useHistory } from "react-router-dom";
import { useState } from "react";
import { ip, port } from "../url";

axios.defaults.withCredentials = true;

const SignUp = () => {
  const [userName, setUserName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const history = useHistory();
  // States 17-28
  const onChangeUserName = (e) => {
    setUserName(e.target.value);
  };
  const onChangeEmail = (e) => {
    setEmail(e.target.value);
  };
  const onChangePassword = (e) => {
    setPassword(e.target.value);
  };
  const onChangeConfirmPassword = (e) => {
    setConfirmPassword(e.target.value);
  };
  //   
  const handleSignup = async () => {
    if (!userName || !email || !password) {
      setErrorMessage("  ");
    } else {
      await axios.post(ip + port + "/user/signup", {
        username: userName,
        email: email,
        password: password,
      });
      history.push("/");
    }
  };
  //    43-61
  const doesPasswordMatch = () => {
    return password === confirmPassword;
  };

  const confirmPasswordClassName = () => {
    if (confirmPassword) {
      return doesPasswordMatch() ? "is-valid" : "is-invalid";
    }
  };

  const renderFeedbackMessage = () => {
    if (confirmPassword) {
      if (!doesPasswordMatch()) {
        return <div className="SignUps__passwordInvalid">  </div>;
      } else if (doesPasswordMatch()) {
        return <div className="SignUps__passwordValid"> </div>;
      }
    }
  };

  return (
    <center className="SignUps__area">
      <div className="SignUps">
        <div className="SignUps__body">
          <div className="SignUps__title"> .</div>
          <div className="SignUps__name SignUp">
            <input className="SignUps__input" type="text" placeholder=" " onChange={onChangeUserName}></input>
          </div>
          <div className="SignUps__email SignUp">
            <input className="SignUps__input" type="email" placeholder="" onChange={onChangeEmail}></input>
          </div>
          <div className="SignUps__passwordA SignUp">
            <input className="SignUps__input" type="password" placeholder="" onChange={onChangePassword}></input>
          </div>
          <div className="SignUps__passwordB SignUp">
            <input className={`SignUps__input ${confirmPasswordClassName()}`} type="password" placeholder=" " onChange={onChangeConfirmPassword}></input>
            {renderFeedbackMessage()}
          </div>
          <button className="SignUps__btn" type="submit" onClick={handleSignup}>
            
          </button>
          <div className="signUp__error">{errorMessage}</div>
        </div>
      </div>
    </center>
  );
};

export default withRouter(SignUp);
