import { Link } from "react-router-dom";

const date = new Date();
const hours = date.getHours();
const minutes = date.getMinutes();
const seconds = date.getSeconds();


const MainClock = () => {
  return (
    <div className="MainClocks">
      <div className="MainClocks__text"> .</div>
      <div className="MainClock__time">  {hours < 10 ? `0${hours}`:`${hours}`} : {minutes < 10 ? `0${minutes}`:`${minutes}`} : {seconds < 10 ? `0${seconds}`:`${seconds}`} </div>
      <Link to="/Clock" className={`MainClocks__link LinkedDetail`}>
            {hours < 10 ? `0${hours}`:`${hours}`} : {minutes < 10 ? `0${minutes}`:`${minutes}`} : {seconds < 10 ? `0${seconds}`:`${seconds}`}
      </Link>
      <div></div>
    </div>
  );
};

export default MainClock;
